import  React,{Component} from "react"

class Header extends Component{
   
   render(){ const client="Programmer";
    const title={
        Designer:"Designe",
        Programmer:"Programming"
    }
    const info={
        name:"Benabdallah",
        nick:"Slimane"
    }
    return(
        <div>
            <div className="navbar bg-dark rounded text-white">
               Hello, {`I am ${info.nick}  ${info.name}`}
                <h1 >This is my first application in React from Homsi instractor.</h1>
             </div>
             <div className="navbar bg-primary rounded">
                 <h2 className= "text-white">{client === "Designer" ? title.Designer:title.Programmer} is my life.
                 </h2>
             </div>
         </div>
    );
}
}
export default Header
