import  React, {Component} from "react"
import DetailItem from "./DetailItem";
import memberInfo from "./memberInfo";

class Details extends Component{
    constructor(){
        super()
        this.state={
            info : memberInfo,
            newMembers: [],
            index :0
        }
        this.clickEvent=this.clickEvent.bind(this);
    }
   
    clickEvent (){
       if(this.state.index < this.state.info.length)  { 
      
      

           this.state.newMembers.push(this.state.info[this.state.index]);
          
           
           this.members= this.state.newMembers.map(member=> 
            <DetailItem key={member.id} 
                               name={member.name}  
                               major={member.major} 
                               phone={member.phone} 
                               email={member.email} 
                               website={member.website} 
                              photo={member.photo}
             />)
           this.setState(prevState=> {return{ index: prevState.index+1}
           
        }) 
        }       
    }
    
      
        
      
    render(){
     
    return(
        <div className="row">
            <div className="col-md-12">
            <button className="btn btn-primary  btn-lg btn-block " onClick={this.clickEvent}>Click me!</button>

                    
            </div>  
            {this.members}              
           </div>  
        
        );
    }
}
export default Details